.App {
    height: 100vh;
    width: 100vw;
    background: local linear-gradient(to top, #6566ab, #000000);
}
.app__content{
    padding: 30px;
    height: 100%;
    width: 100%;
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:30px;
}

.app__logo {
    height: 54px;
    display: block;
    width: 196px;
}

.app__main{
    display: flex;
    flex-direction: row;
    align-items: center;
    max-width: 968px;
    gap:15px;
}

.app__gif{
    width: 100%;
    height: auto;
    border: 3px solid #51cbe6;
    border-radius: 8px;
}

.app__text {
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    font-family: 'Gloria Hallelujah', cursive;
    font-size: 18px;
    gap:10px;
    color: #ffffff;
}

.app__text__header{
    font-weight: 700;
}
.app__text__main{
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.app__nft_data{
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    color: #fff;
    flex-wrap: wrap;
    gap: 50px;
}
.app__footer{
    display: flex;
    flex-direction: column;
    font-family: Passion One,sans-serif;
    width: 100%;
}
.app__footer__header{
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.app__footer__header_title{
    color: #51cbe6;
    font-size: 24px;
}
.app__footer__header_percent{
    font-size: 32px;
}
.app__footer__header_counter{
    color: #51cbe6;
    font-size: 24px;
}
.app__footer__mint_button{
    padding: 4px;
    border-radius: 8px;
    font-family: Passion One,sans-serif;
    background-color: #51cbe6;
    cursor: pointer;
    color: #fff;
    font-size: 34px;
    outline: none;
    border: 1px solid #51cbe6;
    transition: opacity .3s;
}

.app__footer__mint_button:hover{
    opacity: 70%;
}

.app__socials{
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 15px;
    text-decoration: none;
}

.app__socials__item {
    text-decoration: none;
    font-family: Passion One,sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    outline: none;
    border-radius: 8px;
    gap: 8px;
    color: #fff;
    text-transform: uppercase;
    font-size: 24px;
    transition: background-color .3s;
}

.app__socials__item:hover{
background-color: #51cbe6;
    color: #fff;
}

.app__modal__close{
    position: absolute;
    top:0;
    left:0;
    height: 100%;
    width:100%;
    background-color: #0006;
    transition-duration: .2s;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.app__modal__mint__main{
    visibility: visible;
    width: 444px;
    height:426px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    gap: 10px;
    flex-wrap: wrap;
    padding: 30px;
    background: #6667AB;
    color: #ffffffe6;
    font-family: var(--font-family);
    border-radius: 24px;
    font-weight: 700;
}

.app__modal{
    position: absolute;
    left:0;
    top:0;
    display: flex;
    height: 100vh;
    width:100vw;
    visibility: hidden;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.app__modal__image{
    height: auto;
    max-width: 176px;
    border-radius: 20px;
    border: 1px solid #fff;
}

.app_modal__h__text{
    color:white;
    font-weight: normal;
}

.app_modal__p__text{
    color:lightgray;
    font-weight: normal;
    width: auto;
    height: auto;
}

.app__submodal__mint__main{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-evenly;
}

.app__submodal__item__main
{
    display: block;
    flex-direction: column;
    gap: 15px;
    align-items: center;
    text-align: center;
}
.app__modal__mint__config{
    width: 40%;
}

@media (max-width: 500px) {
    .App {
        height: 100%;
        width: 100%;
        background: local linear-gradient(to top, #6566ab, #000000);
    }

    .app__main{
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 968px;
        gap:15px;
    }

    .app__modal{
        position: absolute;
        left:0;
        top:0;
        display: flex;
        height: 100vh;
        width:100%;
        visibility: hidden;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    .app__modal__mint__main{
        visibility: visible;
        width: 100%;
        height:auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        gap: 10px;
        flex-wrap: wrap;
        padding: 30px;
        background: #6667AB;
        color: #ffffffe6;
        font-family: var(--font-family);
        border-radius: 24px;
        font-weight: 700;
    }

    .app__modal__close{
        height: 100%;
        width: 100%;
        position: fixed;
        background: fixed  #0006;
        --moz-background-size:100%;
        -webkit-background-size:100%;
        -o-background-size:100%;
        background-size:100%;
        transition-duration: .2s;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

}

